export const CouponForm = (props) => {
	// TODO: Get cost form server
	const originalCost = 149;
	const [verifiedCode, setVerifiedCode] = useState(false);
	const [error, setError] = useState();
	const [totalCost, setTotalCost] = useState(originalCost);
	const [discount, setDiscount] = useState(0);
	const [appliedCoupon, setAppliedCoupon] = useState("");

	const formic = useFormik({
		initialValues: {
			code: "",
		},
		onSubmit: () => {
			props.onSubmit(appliedCoupon, props.token);
		},
		validate: (values) => {
			let errors = {};
			return errors;
		},
	});

	const validate = () => {
		if(appliedCoupon === ""){
		const code = formic.values.code;
		CQAPI.verifyCoupon(code, props.token)
			.then((resp) => {
				setVerifiedCode(true);
				setDiscount(resp.discount);
				setTotalCost((originalCost - resp.discount).toString());
				setError(null);
				setAppliedCoupon(code);
			})
			.catch((err) => {
				let errorMessage = err.toString();
				if (errorMessage.substring(0, 7) === "Error: ") {
					errorMessage = errorMessage.substring(7);
				}
				setVerifiedCode(false);
				setError(errorMessage);
				setTotalCost(originalCost);
				setDiscount(0);
			});
		}
		else {
			setAppliedCoupon("");
			setVerifiedCode(false);
			setTotalCost(originalCost);
			setDiscount(0);
			formic.setValues({code: ""});
		}
		// const [validated, cost, cred] = props.toValidate(formic.values.code)
	};
	return (
		<Form onSubmit={formic.handleSubmit}>
			<Container>
				<Row>
					<Col>
						<Form.Label className="font-weight-bold">
							Your Details
						</Form.Label>
						<br />
						<br />
						<ul className="list-unstyled">
							<li className="mb-4 text-secondary">
								{`${props.user.firstName} ${props.user.lastName}` ||
									"Sample"}
							</li>
							<li className="mb-4 text-secondary">
								{props.user.mobile.number || "Sample"}
							</li>
						</ul>
					</Col>
					<Col>
						<Form.Label className="font-weight-bold">
							Your Report
						</Form.Label>
						<br />
						<br />
						<ul className="list-unstyled">
							<li className="mb-4 text-secondary">
								CQ Assessment:&nbsp;&nbsp;&#x20b9;
								{originalCost || "149"}
							</li>

							{verifiedCode && (
								<li className="mb-4 text-secondary">
									Discount:&nbsp;&nbsp;&#x20b9;
									{discount || "0"}
								</li>
							)}
							<li className="mb-4 text-secondary font-weight-bold">
								Order total:&nbsp;&nbsp;&#x20b9;
								{totalCost || "149"}
							</li>
						</ul>
					</Col>
				</Row>
				<br />
				<br />
			</Container>
			<Form.Label>Coupon Code</Form.Label>
			<Form.Group controlId="code" className="position-relative">
				{error ? <Alert variant="danger" onClose={()=>setError(null)} dismissible>{error}</Alert> : null}
				{verifiedCode ? (
					<Alert variant="success">
						Coupon applied successfully!
					</Alert>
				) : null}
				<Row noGutters>
					<Col>
						<Form.Control
							value={formic.values.code}
							onChange={formic.handleChange}
							onBlur={formic.handleBlur}
							type="text"
							placeholder="Enter coupon code"
							disabled={appliedCoupon !== ""}
						/>
					</Col>
					<Col sm={3} style={{textAlign:"right"}}>
						<Link style={{verticalAlign: "middle"}} onClick={validate}>{appliedCoupon !== ""? "CLEAR" : "APPLY"} CODE</Link>
					</Col>
				</Row>
			</Form.Group>
			<br />
			<br />
			<div className="text-center">
				<Button type="submit" variant="primary" disabled={(formic.values.code && !verifiedCode) }>
				{props.submitText || "Unlock Full Report"}
			</Button>
			{(formic.values.code && !verifiedCode) ? <p className="text-muted">Apply valid coupon code or clear coupon code to continue</p> : null}
			</div>
		</Form>
	);
};

export const LoginWithPassword = (props) => {
	const [timeLeft, setTimeLeft] = useState(30);
	const [canRequestOTP, setCanRequestOTP] = useState(false);
	const formic = useFormik({
		initialValues: {
			password: "",
		},
		onSubmit: (values, user) => {
			// console.log(values)
			props.onSubmit(values.password);
		},
		validate: (values) => {
			let errors = {};
			if (!values.password) {
				errors.password = "password required";
			}
			return errors;
		},
	});
	return (
		<Form onSubmit={formic.handleSubmit}>
			<p>Enter Password</p>
			<p className="text-muted">
				<small>Enter the Password</small>
			</p>
			{props.passwordErrors ? (
				<Alert variant="danger">{props.passwordErrors}</Alert>
			) : null}
			<Form.Group controlId="password">
				{formic.errors.password && formic.touched.password ? (
					<Alert variant="danger">{formic.errors.password}</Alert>
				) : null}
				<Form.Control
					value={formic.values.password}
					onChange={formic.handleChange}
					onBlur={formic.handleBlur}
					type="password"
					placeholder="Password"
					required
				/>
			</Form.Group>
			{/* <Row>
				<Col className="text-right">
					<Button
						className="text-gold"
						size="sm"
						variant="link"
						style={{ paddingLeft: 0 }}
						onClick={props.onOTPRequest}
					>
						Reset Password
					</Button>
				</Col>
			</Row> */}
			<br />
			<br />
			<Button type="submit" variant="primary">
				Login
			</Button>
		</Form>
	);
};
