import React, { useEffect, useState } from "react";
import { Alert, Form, Button, Container, Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { useFormik } from "formik";
import CQAPI from "../api";

const OTPForm = (props) => {
	const [timeLeft, setTimeLeft] = useState(20);
	const [canRequestOTP, setCanRequestOTP] = useState(false);
	useEffect(() => {
		const timer = setTimeout(() => {
			if (timeLeft < 1) {
				setCanRequestOTP(true);
				return;
			}
			setTimeLeft(timeLeft - 1);
		}, 1000);
		return () => clearTimeout(timer);
	});
	const formic = useFormik({
		initialValues: {
			otp: "",
		},
		onSubmit: (values, user) => {
			// console.log(values)
			props.onSubmit(values.otp);
		},
		validate: (values) => {
			let errors = {};
			if (!values.otp) {
				errors.otp = "OTP required";
			}
			return errors;
		},
	});
	const resendOTP = () => {
		props.resendOTP();
		setTimeLeft(20);
		setCanRequestOTP(false);
	};
	return (
		<Form onSubmit={formic.handleSubmit}>
			<p>Enter OTP</p>
			<p className="text-muted">
				<small>
					Enter the{" "}
					{`${
						props.loginMessage
							? props.loginMessage
							: "OTP sent to your registered mobile number"
					}`}
				</small>
			</p>
			<Form.Group controlId="otp">
				{formic.errors.otp && formic.touched.otp ? (
					<Alert variant="danger">{formic.errors.otp}</Alert>
				) : null}
				<Form.Control
					value={formic.values.otp}
					onChange={formic.handleChange}
					onBlur={formic.handleBlur}
					type="text"
					placeholder="OTP"
					required
				/>
			</Form.Group>
			<Row>
				<Col>
					<div>
						<small>
							00:
							{timeLeft.toLocaleString("en-US", {
								minimumIntegerDigits: 2,
							})}
						</small>
					</div>
				</Col>
				<Col className="text-right">
					<Button
						className="text-gold"
						size="sm"
						variant="link"
						disabled={!canRequestOTP}
						style={{ paddingLeft: 0 }}
						onClick={resendOTP}
					>
						Resend OTP
					</Button>
				</Col>
			</Row>
			<br />
			<br />
			<Button type="submit" variant="primary">
				{props.submitText}
			</Button>
		</Form>
	);
};

function mapStateToProps(state) {
	return {
		loginMessage: state.auth.loginMessage,
	};
}

export default connect(mapStateToProps)(OTPForm);
